@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.Die {
  color: white;
  text-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.Die-locked {
  opacity: 0.5;
  text-shadow: none;
}

.Die:not(.Die-locked):hover {
  cursor: pointer;
  opacity: 0.8;
}
.Die-rolling {
  animation: spin 1s ease-out;
}

.Die-rolling:hover,
.Die[disabled] {
  cursor: not-allowed;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.Dice {
  margin: 1em;
  display: flex;
  justify-content: space-between;
}

.RuleRow {
  transition: all 0.2s;
}
.RuleRow-active:hover {
  cursor: pointer;
  background: rgba(227, 242, 253, 0.5);
}

.RuleRow-disabled {
  background: linear-gradient(
    to right,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 0%,
    rgba(209, 196, 233, 1) 30%,
    rgba(227, 242, 253, 1) 40%
  );
  background-size: 300% 300%;
  animation: Slide 1s ease 1;
}

@keyframes Slide {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 51%;
  }
}
.RuleRow-disabled td:first-child {
  text-decoration: line-through;
}

.RuleRow-disabled:hover {
  cursor: not-allowed;
}

.RuleRow td {
  padding: 0.3em;
  border-bottom: solid 1px black;
}

.RuleRow-name {
  width: 50%;
  text-align: left;
}

.RuleRow-score {
  width: 50%;
  text-align: right;
}

.ScoreTable {
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
  width: 80%;
  background-color: white;
  padding: 1em;
}

.ScoreTable table {
  width: 100%;
  margin: 0 auto;
  min-width: 400px;
}

.ScoreTable-section {
  vertical-align: top;
  width: 100%;
}

.Game {
  width: 30%;
  background: white;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  margin: 0 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h2 {
  font-weight: 100;
  font-size: 2em;
  border-bottom: 2px solid purple;
  display: inline-block;
}
.Game-dice-section {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
}

.Game-button-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Game-reroll {
  font-size: 2em;
  color: white;
  font-weight: 100;
  transition: 0.5s;
  background-size: 200% auto;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(
    to right,
    #91eae4 0%,
    #7f7fd5 51%,
    #91eae4 100%
  );
  border-radius: 0.5rem;
  border: none;
  width: 50%;

  margin-bottom: 2rem;
}

.Game-reroll:hover {
  cursor: pointer;
  background-position: right center;
}

.Game-reroll:active,
.Game-reroll:focus {
  outline: none;
}

.Game-reroll:disabled {
  background-color: #ddd;
  cursor: not-allowed;
  opacity: 0.5;
}

.Game-description {
  font-style: italic;
  color: white;
}

.Game-header {
  width: 100%;
  background: linear-gradient(-45deg, #673ab7, #9c27b0);
  background-size: 400% 400%;
  animation: Gradient 15s ease infinite;
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.App-title {
  margin-top: 1rem;
  color: white;
  margin-bottom: 0;
  font-weight: 100;
  display: inline-block;
  font-size: 5rem;
  width: 50%;
}

@media only screen and (min-width: 320px) and (max-width: 768px) {
  .Game {
    width: 100%;
    background: white;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
    align-items: center;
    height: 100%;
  }

  .Game-header {
    width: 100%;
    background: linear-gradient(-45deg, #3a7fb7, #2785b0);
    background-size: 400% 400%;
    animation: Gradient 15s ease infinite;
    min-width: 100%;
  }

  .App-title {
    margin-top: 1rem;
    color: white;
    margin-bottom: 0;
    font-weight: 100;
    display: inline-block;
    font-size: 3rem;
    width: 100%;
  }

  .Game-dice-section {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
  }
  .fa-5x {
    font-size: 3.4rem !important;
  }
  .ScoreTable {
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
    width: 100%;
    background-color: white;
    padding: 0.5rem;
  }
  .ScoreTable-section {
    vertical-align: top;
    width: 100%;
  }
  .ScoreTable table {
    width: 100%;
    margin: 0;
    min-width: 300px;
    padding: 0 0.5rem;
  }
  .RuleRow td {
    padding: 0.1rem;
    font-size: 0.9rem;
    border-bottom: solid 1px black;
  }
  .RuleRow-name {
    width: 50%;
    text-align: left;
  }
  .RuleRow-score {
    width: 50%;
    text-align: right;
  }
  .ScoreTable-section {
    vertical-align: top;
    width: 100%;
  }
  .Dice {
    padding: 1rem 0.25rem;
    display: flex;
    justify-content: space-between;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1366px) {
  .Game {
    width: 75%;
    margin-top: 4rem;
  }
  .Game-header {
    min-width: 100%;
  }
  .RuleRow td {
    padding: 0.1rem;
    font-size: 1.1rem;
    border-bottom: solid 1px black;
  }
  .fa-5x {
    font-size: 4.5rem !important;
  }
}

@media only screen and (min-width: 411px) and (max-width: 426px) {
  .Game-header {
    min-width: 100%;
  }
  .fa-5x {
    font-size: 4rem !important;
  }
}

@media only screen and (min-width: 320px) and (max-width: 320px) {
  .Game-header {
    min-width: 100%;
  }
}

html {
  height: 100%;
  overflow: auto;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto";
  font-weight: 100;
  height: 100vh;
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
}

::-webkit-scrollbar {
  display: none;
}

